.container {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.header {
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 34px;
}

.filtering {
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: start;
    padding: 10px 0;
}

.search {
    display: flex;
    width: 100%;
    flex-direction: column;
}

.showPacksCards {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 150px;
}

.table {
    width: 80%;
}

.button {
    height: 20px;
}

.cards {
    display: flex;
}

.tableHeader{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;

}